import './VideoItem.css'

const VideoItem = ({video,selectvideo}) => {
    return (
        <div className="card" onClick = {()=>selectvideo(video)}>
            <img src={video.snippet.thumbnails.medium.url} alt={video.snippet.description} />
            <div className="text">
                {video.snippet.title}
            </div>
        </div>
    )
}

export default VideoItem
