import './Selectionbar.css'

const Selectionbar = ({getvideos}) => {
    return (
        <div className='selection'>
            <button className='roja' onClick={()=>getvideos("roja serial")}>Roja</button>
            <button className='sundari' onClick={()=>getvideos("sundari serial")}>Sundari</button>
            <button className='chandraleka' onClick={()=>getvideos("chandraleka serial")}>ChandraLeka </button>
            <button className='magaraasi' onClick={()=>getvideos("magaraasi serial")}>Magaraasi</button>
            <button className='andevaa' onClick={()=>getvideos("Anbe Vaa serial")}>Anbe Vaa</button>
        </div>
    )
}

export default Selectionbar
