import {useState} from 'react';
import './App.css';
import Selectionbar from './components/Selectionbar';
import Videolist from './components/videolist';
import VideoPlayer from './components/VideoPlayer';
import youtube from './api/youtube';
import axios from 'axios';

function App() {

const [videos,setVideos] = useState([]);
const [selectedvideo,setSelectedVideo] = useState(null)

const getvideos=async (search) =>{
  const pre = await axios.get('https://jsonplaceholder.typicode.com/todos/1').then(res=>console.log('res'));
  const response = await youtube.get('https://www.googleapis.com/youtube/v3/search',
  {
    params:{
      q:search
    }
  })

  setVideos(response.data.items);
  console.log(response)

}
const selectvideo = (video) =>
{
  console.log(video);
  setSelectedVideo(video);
}



  return (
    <div className="App">
      <Selectionbar getvideos={getvideos} />
      {
        selectedvideo?<VideoPlayer video = {selectedvideo} />:null
      
}{
      videos.length>0?<Videolist videos={videos} selectvideo={selectvideo}/>:null
    }
    </div>
  );
}

export default App;
